let local = localStorage;
/** 
 * 
 */
export default {
    /**
     * key
     * @param {*} key 
     */
    getItem (key) {
        try {
            return JSON.parse(local.getItem(key));
        } catch (error) {
            return null;
        }
    },
    /**
     * 
     * @param {*} key 
     * @param {*} value 
     */
    setItem (key, value) {
        local.setItem(key, JSON.stringify(value))
    },
    /**
     * 
     */
    clear () {
        local.clear()
    },
    /**
     * key
     * @param {*} key 
     */
    removeItem (key) {
        local.removeItem(key)
    },
}